body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 14px;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 10px 0;
}

h1 {
  font-size: 30px;
}

h2 {
  font-size: 26px;
}

h3 {
  font-size: 20px;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.video {
  position: relative;
  height: 100vh;
  max-height: 665px;
  overflow: hidden;
  margin-left: 86px;
}
@media (max-width: 480px) {
  .video {
    margin-left: 79px;
  }
}
.video .overlay {
  z-index: 9;
  position: absolute;
  background: rgb(191, 154, 42);
  background: linear-gradient(162deg, rgba(191, 154, 42, 0.3113620448) 0%, rgba(244, 215, 127, 0.2469362745) 53%, rgba(125, 95, 6, 0.3505777311) 100%);
  height: 100%;
  max-height: 100%;
  width: 100%;
  display: flex;
  justify-content: left;
  align-items: center;
}
@media (max-width: 1140px) {
  .video .overlay {
    min-height: 100%;
  }
}
.video video {
  width: 100%;
  display: block;
  filter: blur(4px);
}
@media (max-width: 1140px) {
  .video video {
    width: auto;
    min-height: 100%;
  }
}

.contenido-video {
  z-index: 10;
}
.contenido-video .video-logo {
  width: 110px;
  max-height: 125px;
}
@media (max-width: 480px) {
  .contenido-video .video-logo {
    position: absolute;
    right: 400px;
  }
}
.contenido-video .video-flex {
  display: flex;
  justify-content: left;
  align-items: center;
  gap: 2rem;
  margin-bottom: 1rem;
  width: auto;
}
@media (max-width: 480px) {
  .contenido-video .video-flex {
    display: inline-block;
  }
}
.contenido-video .video-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 1rem;
}
.contenido-video h2 {
  color: #f2cf35;
  font-size: 3.6rem;
  margin: 0;
  text-shadow: 0 0 2px #000000;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
}
@media (max-width: 480px) {
  .contenido-video h2 {
    font-size: 2.5rem;
  }
}
.contenido-video p {
  font-size: 1.2rem;
}

.video-p {
  background-color: transparent;
  display: inline-block;
  padding: 0.5rem 2rem;
  max-width: 36rem;
  margin-left: 117px;
}
.video-p p {
  color: #000000;
  font-size: 1.8rem;
  font-weight: 700;
  font-family: "Comfortaa", cursive;
}
@media (max-width: 480px) {
  .video-p p {
    font-size: 1rem;
  }
}
@media (max-width: 768px) {
  .video-p {
    margin-left: 0;
  }
}
@media (max-width: 480px) {
  .video-p {
    padding: 0;
  }
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --main-color: #eeeeee;
  --main-color-dark: #000;
  --main-color-light: #000;
  --text-color: #000;
}

body {
  background-color: #e7e7ea;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

nav {
  z-index: 11;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  background-color: var(--main-color);
  width: 16rem;
  padding: 1.8rem 0.85rem;
  color: #000;
  display: flex;
  flex-direction: column;
  transition: width 0.5s ease-in-out;
  border-right: 1px solid #939393;
}

nav::before {
  content: "";
  position: absolute;
  width: 2rem;
  height: 100%;
  top: 0;
  left: 100%;
}

main {
  flex: 1;
  padding: 2rem;
  color: #1f2027;
  display: flex;
  flex-direction: column;
}

main h1 {
  margin-bottom: 1rem;
}

main .copyright {
  margin-top: auto;
  font-size: 0.9rem;
}

main .copyright span {
  color: var(--main-color);
  font-weight: 500;
  cursor: pointer;
}

.sidebar-links {
  white-space: nowrap;
  overflow: hidden;
}

.sidebar-top {
  position: relative;
  display: flex;
  align-items: center;
}

.sidebar-top .logo {
  width: 2.9rem;
  margin: 0 0 0 0.4rem;
}

.sidebar-top h3 {
  /* padding-left: 0.5rem; */
  font-weight: 600;
  font-size: 1.2rem;
  padding-left: 1rem;
  padding-top: 0.5rem;
  white-space: nowrap;
  overflow: hidden;
}

.shrink-btn {
  position: absolute;
  top: 50%;
  height: 27px;
  width: 26px;
  padding: 0 0.3rem;
  background-color: var(--main-color);
  border-radius: 6px;
  cursor: pointer;
  box-shadow: 0 3px 10px -3px rgba(70, 46, 118, 0.3);
  right: -2.65rem;
  transform: translateY(-50%) translateX(-8px);
  opacity: 0;
  pointer-events: none;
  transition: 0.3s;
}

.shrink-btn svg {
  min-height: 16px;
  min-width: 26px;
  margin-top: 0.3rem;
  padding-right: 0.5rem;
  line-height: 27px;
  transition: 0.3s;
}

/* .shrink-btn:hover {
  background-color: var(--main-color-dark);
} */
nav:hover .shrink-btn,
.shrink-btn.hovered {
  transform: translateY(-50%) translateX(0px);
  opacity: 1;
  pointer-events: all;
}

.search {
  min-height: 2.7rem;
  background-color: #eee;
  margin: 0;
  display: grid;
  grid-template-columns: 2.7rem 1fr;
  align-items: center;
  text-align: center;
  border-radius: 50px;
  /* cursor: pointer; */
}

.search input {
  height: 100%;
  border: none;
  background: none;
  outline: none;
  color: #fff;
  caret-color: #fff;
  font-family: inherit;
}

.search input::placeholder {
  color: var(--main-color-dark);
}

.sidebar-links ul {
  position: relative;
}

.sidebar-links li {
  position: relative;
  padding: 2.5px 0;
}

.sidebar-links a {
  color: var(--text-color);
  font-weight: 400;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  height: 53px;
}

.icon {
  font-size: 1.3rem;
  text-align: center;
  min-width: 3.7rem;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
}

.icon i {
  grid-column: 1/2;
  grid-row: 1/2;
  transition: 0.3s;
}

.icon i:last-child {
  opacity: 0;
  color: #fff;
}

.sidebar-links {
  padding-top: 2rem;
}

.sidebar-links a.active,
.sidebar-links a:hover {
  color: #000000;
}

.sidebar-links a .link {
  transition: opacity 0.3s 0.2s, color 0.3s;
}

.sidebar-links a.active i:first-child {
  opacity: 0;
}

.sidebar-links a.active i:last-child {
  opacity: 1;
}

.active-tab {
  width: 100%;
  height: 53px;
  background-color: #f2cf35;
  border-radius: 10px;
  position: absolute;
  top: 3.5px;
  left: 0;
  transition: top 0.3s;
}

.active-tab1 {
  width: 100%;
  height: 53px;
  background-color: #f2cf35;
  border-radius: 10px;
  position: absolute;
  top: 60.5px;
  left: 0;
  transition: top 0.3s;
}

.active-tab2 {
  width: 100%;
  height: 53px;
  background-color: #f2cf35;
  border-radius: 10px;
  position: absolute;
  top: 118.5px;
  left: 0;
  transition: top 0.3s;
}

.active-tab3 {
  width: 100%;
  height: 53px;
  background-color: #f2cf35;
  border-radius: 10px;
  position: absolute;
  top: 176.5px;
  left: 0;
  transition: top 0.3s;
}

.active-tab4 {
  width: 100%;
  height: 53px;
  background-color: #f2cf35;
  border-radius: 10px;
  position: absolute;
  top: 234.5px;
  left: 0;
  transition: top 0.3s;
}

.sidebar-links h4 h3 {
  position: relative;
  font-size: 0.8rem;
  text-transform: uppercase;
  font-weight: 600;
  padding: 0 0.8rem;
  color: var(--text-color);
  letter-spacing: 0.5px;
  height: 45px;
  line-height: 45px;
  transition: opacity 0.3s 0.2s, height 0.5s 0s;
}

.sidebar-footer {
  position: relative;
  margin-top: auto;
}

.account {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3rem;
  color: var(--text-color);
  height: 53px;
  width: 3.7rem;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s 0s, color 0.3s 0s;
}

