.tooltip {
  background-color: var(--main-color);
  position: absolute;
  right: -1.2rem;
  top: 0;
  transform: translateX(100%) translateY(-50%);
  padding: 0 0.8rem;
  font-size: 0.85rem;
  display: none;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr;
  height: 30px;
  align-items: center;
  border-radius: 7px;
  box-shadow: 0 3px 10px -3px rgba(70, 46, 118, 0.3);
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  text-align: center;
  white-space: nowrap;
}

.tooltip span {
  grid-column: 1/2;
  grid-row: 1/2;
  opacity: 0;
  transition: 0.3s;
}

.tooltip span.show {
  opacity: 1;
}

.tooltip-element:hover ~ .tooltip {
  opacity: 1;
  pointer-events: all;
}

/* When the menu shrinks */
.hide {
  transition: opacity 0.3s 0.2s;
}

body.shrink nav {
  width: 5.4rem;
}
@media (max-width: 480px) {
  body.shrink nav {
    width: 4.9rem;
  }
}

body.shrink .hide {
  opacity: 0;
  pointer-events: none;
  transition-delay: 0s;
}

body.shrink .shrink-btn svg {
  transform: rotate(-180deg);
  padding-right: 0;
  padding-left: 0.5rem;
}

body.shrink .sidebar-links h4 h3 {
  height: 10px;
}

@media (max-width: 480px) {
  .active svg {
    padding-right: 0.5rem;
  }
}

body.shrink .tooltip {
  display: grid;
}

.showcase {
  margin-left: 86px;
}
@media (max-width: 480px) {
  .showcase {
    margin-left: 79px;
  }
}

.showcase-display {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin: 1rem 0;
  max-height: auto;
  width: auto;
  overflow: hidden;
}
@media (max-width: 768px) {
  .showcase-display {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
@media (max-width: 480px) {
  .showcase-display {
    width: auto;
  }
}
.showcase-display .content-text {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  padding: 0;
  background-color: #eeeeee;
  height: 650px;
  max-width: 760px;
}
@media (max-width: 1440px) {
  .showcase-display .content-text {
    height: 650px;
    max-width: 760px;
  }
}
@media (max-width: 768px) {
  .showcase-display .content-text {
    width: 100%;
  }
}
.showcase-display .content-text p {
  font-size: 1.1rem;
  padding: 0 20px;
}
.showcase-display .content-text span {
  font-size: 1.7rem;
  font-weight: 700;
  font-family: "Comfortaa", cursive;
}
.showcase-display .content-text span:last-of-type {
  font-size: 1.5rem;
  font-weight: 500;
  padding-bottom: 0.5rem;
}
.showcase-display .content-img {
  max-width: 560px;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  height: 650px;
}
.showcase-display .content-img img {
  max-height: 650px;
}
@media (max-width: 768px) {
  .showcase-display .content-img {
    width: 100%;
  }
}

.one, .two, .three {
  position: relative;
  left: 577.33px;
}

.eno, .owt, .eerht {
  position: relative;
  right: 577.33px;
}

.first, .second, .third {
  opacity: 0;
  transition: opacity 2s ease;
}

div.ani1 {
  opacity: 1;
}

div.ani1 .thefirst {
  animation: theOne 0.6s ease-in-out forwards;
}

div.ani1 .firstthe {
  animation: oneThe 0.6s ease-in-out forwards;
}

@media (max-width: 768px) {
  .one {
    left: 0;
  }

  .eno {
    right: 0;
  }

  .first, .second, .third {
    opacity: 1;
  }

  div.ani1 .thefirst {
    animation: none 0.6s ease-in-out forwards;
  }

  div.ani1 .firstthe {
    animation: none 0.6s ease-in-out forwards;
  }
}
@keyframes theOne {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-577.33px);
  }
}
@keyframes oneThe {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(577.33px);
  }
}
div.ani2 {
  opacity: 1;
}

div.ani2 .thesecond {
  animation: theTwo 0.6s ease-in-out forwards;
}

div.ani2 .secondthe {
  animation: twoThe 0.6s ease-in-out forwards;
}

@media (max-width: 768px) {
  .two {
    left: 0;
  }

  .owt {
    right: 0;
  }

  div.ani2 .thesecond {
    animation: none 0.6s ease-in-out forwards;
  }

  div.ani2 .secondthe {
    animation: none 0.6s ease-in-out forwards;
  }
}
@keyframes theTwo {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-577.33px);
  }
}
@keyframes twoThe {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(577.33px);
  }
}
div.ani3 {
  opacity: 1;
